.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1500px;
}

.home__image {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__row {
  display: flex;
  z-index: -1;
  margin-left: 5px;
  margin-right: 5px;
}

.home__vid {
  object-fit: contain;
  width: 100%;
}

.video-react-big-play-button {
  display: none !important;
}

@media only screen and (max-width: 700px) {
  .home__row {
    flex-direction: column;
  }
}
